// variables
@import 'project/variables_project';

/*********************************************************
 *                                                       *
 * Définition des styles du simulateur (partie centrale) *
 *                                                       *
 *********************************************************/

.step-page {
  overflow: hidden;
}

.step-container {
  overflow: hidden;
  padding: 1em;
  margin-bottom: 1.5em;
  background: $gray-light;
  &#choix-fieldset-2 {
    padding: 0;
  }
}

.field-container {
  overflow: hidden;
  margin: 0.5em 0;
}

.field-container br {
  display: none;
}

/*********************************************************
 * Texte de présentation du simulateur(sous le titre)   *
 *********************************************************/
#core div.intro {
  margin-bottom: 1.5em;
}
#core div.intro p {
  text-align: justify;
}

/***************************************************************
 * Fil d'ariane du déroulement des Ã©tapes :                  *
 ***************************************************************/

.breadcrumb-simulateur {
  display: inline;
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
  border: 0;
}

.breadcrumb-simulateur ol {
  display: table;
  padding: 0;
  width: 100%;
}

.breadcrumb-simulateur li {
  display: table-cell;
}

.breadcrumb-simulateur li p {
  position: relative;
  display: block;
  text-align: center;
  text-decoration: none;
  color: #fff;
  background: $blue-accent;
}
.breadcrumb-simulateur li.current.current p {
  color: #000;
  background: #e9e9e9;
}
.breadcrumb-simulateur li.current.current p:after {
  border-left-color: #e9e9e9;
}
.breadcrumb-simulateur li:first-child p:before {
  display: none;
}
.breadcrumb-simulateur li:last-child p:after {
  height: 100%;
  border: 0;
  background: $blue-accent;
}
.breadcrumb-simulateur li.current:last-child p:after {
  background: #fff;
}
.breadcrumb-simulateur li p {
  margin: 0 10px 0 0;
  padding: 15px 5px;
  height: 50px;
}
.breadcrumb-simulateur li p:after {
  position: absolute;
  content: '';
  top: 0;
  right: -24px;
  border-top: 25px solid transparent;
  border-bottom: 25px solid transparent;
  border-left: 25px solid $blue-accent;
  z-index: 1;
}
.breadcrumb-simulateur li p:before {
  position: absolute;
  content: '';
  top: 0;
  left: 0;
  border-top: 25px solid transparent;
  border-bottom: 25px solid transparent;
  border-left: 25px solid #fff;
}
.breadcrumb-simulateur li:last-child p {
  margin: 0;
}
@-moz-document url-prefix() {
  .step-page .breadcrumb-simulateur li:last-child p {
    position: relative;
    top: -18px;
  }
}

/*********************************************************
 * Texte de description de l'étape                       *
 * en dessous du fil d'ariane                            *
 *********************************************************/
.step-page .step-description {
  margin-bottom: 1.5em;
}
.step-page .step-description p {
  margin: 0;
  padding: 0;
}
.step-page .mention-asterisk {
  margin: 0 0 1em;
  padding: 0;
}
.step-page .mention-asterisk .asterisk {
  color: $red-dark;
}
#step4 div.mention-asterisk {
  display: none;
}
.step-page .foot-notes {
  float: none;
  clear: both;
  margin: 1em 0;
  padding: 1em;
  color: #fff;
  background: $blue-accent;
}
.step-page .foot-note {
  font-size: 1.1em;
  font-weight: bold;
  text-align: center;
  p {
    margin: 0;
  }
}


/*********************************************************
 * Champs des formulaires                                *
 *********************************************************/
body {
  text-align: left;
}
#ctAccederCmpt,
#ctPoserQuestion {
  text-align: center;
}
.step-page fieldset,
.step-page div.fieldset {
  margin-top: 0;
  margin-bottom: 1.5em;
}
.step-page legend,
.step-page .legend {
  margin-bottom: 0.8em;
  font-size: 1.2em;
  font-weight: bold;
}
.step-page .step-container legend {
  margin: 0 -1em;
  padding: 1em;
  width: 120%;
  background: $gray-light;
}
.step-page .legend p {
  margin-bottom: 0;
}
.step-page #choix-fieldset-2 legend {
  text-align: center;
}
.step-page input {
  height: 1.3em;
}
.step-page input[type=text],
.step-page input[type=date],
.step-page input[class=date],
.step-page input[type=number],
.step-page input[type=money] {
  box-sizing: border-box;
  margin-top: 0;
  margin-right: 0.5em;
  padding-left: 0.2em;
  height: auto;
  line-height: 2em;
  color: $blue-accent;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fff;
}
.step-page input.error {
  font-weight: normal;
  border: 1px solid #c60000;
  background-color: #f5dedb;
}
.step-page .select2-container {
  width: 100%;
}
.step-page .select2-container .select2-choice,
.step-page .select2-drop {
  color: $blue-accent;
}
.step-page select {
  height: 2em;
}
.step-page input[type=money] {
  width: 8em;
  max-width: 8em;
  font-size: 1em;
}
.step-page input[class=date],
.step-page input[type=date],
.step-page input[class~=date].error,
.step-page input[type=date].error {
  width: 6.2em;
  max-width: 6.2em !important;
}
.step-page input[type=text] {
  max-width: 10em;
}
.step-page input[type=number] {
  max-width: 3.5em;
  /* padding:.5em .1em .5em .4em; */ /* pb IE, OPERA, Safari et Chrome si padding*/
}
.step-page input[type=submit],
.step-page input[type=reset] {
  height: auto;
}
.step-page input[disabled=disabled] {
  border: 0;
}
.step-page .choice input[type=radio] {
  margin: 0 0.4em 0 0;
  display: inline-block;
  vertical-align: bottom
}

.step-page .output {
  display: inline-block;
  padding: 0 0.1em;
  vertical-align: baseline;
}
#step4  span[id=montantIm],
#step4  span[id=cumul] {
  margin-top: 0.66em;
  font-size: 1.2em !important;
}

.step-page label,
.step-page .label,
.step-page input {
  display: inline-block;
}
.step-page label,
.step-page .label {
  padding: 0 0 .3em;
  width: 100%;
  font-size: 1em;
  font-weight: bold;
  color: #000;
  text-align: left;
}
#step4 label,
#step4 .label {
  width: 21em;
  min-width: 21em;
}
.step-page label.choice {
  margin-right: 1.2em;
  padding: 0;
  width: auto;
  min-width: 1em;
  font-weight: normal;
  text-align: left;
}
#typeAcheteurPublic-container label {
  text-align: left;
}

.step-page .post-note {
  position: relative;
  clear: both;
  margin: 0;
  padding-top: 0;
  width: 100%;
  font-size: 0.7em !important;
}
.step-page .error {
  font-weight: bold;
  color: $red-dark;
}
.step-page div.error {
  clear: both;
  text-align: center;
}
.step-page fieldset div.error {
  text-align: right;
}
.step-page .emphasized {
  font-weight: bold;
  color: $blue-accent;
}
.step-page .explanation {
  float: right;
  margin-top: 0.95em;
  width: 12em;
  font-weight: bold;
  color: $blue-accent;
}

.step-page br {
  clear: both;
}
.clearer {
  float: none;
  clear: both;
  margin: 0;
  padding: 0;
}


/*********************************************************
 * Date-picker                                           *
 *********************************************************/
.date-picker-control {
  vertical-align: baseline;
}

.date-picker-control:link,
.date-picker-control:visited {
  margin: 0 0.2em;
}

/*********************************************************
 * Bouton aide                                           *
 *********************************************************/
.step-page .btn-help {
  margin: 0 0.5em;
  width: 20px;
  height: 20px;
  vertical-align: baseline;
  border: none;
}
.date-picker-control + .btn-help {
  margin-top: -0.3em;
}
#dateInitiale + .btn-help {
  margin-top: -0.1em;
}
.step-page .help-panel {
  clear: both;
  &:before,&:after {
    display: none;
  }
}

/*********************************************************
 * Money symbol                                          *
 *********************************************************/
span.money-symbol {
  display: inline-block;
  vertical-align: baseline;
}
#montantPrincipalTTC-container .output span.money-symbol,
.step-page .output span.money-symbol {
  margin-top: 0;
}

/*********************************************************
 * Panneau d'aide déroulant sous chaque champ            *
 *********************************************************/
.help-panel p {
  margin-bottom: 0.5em;
  width: 534px;
  width: 100%;
  font-size: 0.9em !important;
}
.help-panel {
  margin-top: 0.7em;
  padding: 0.4em;
  border: 1px solid $gray;
  background: white;
}
.help-panel dt {
  display: none;
}

/*********************************************************
 * Boutons d'action et de navigation dans le simulateur  *
 *********************************************************/
.action_buttons {
  float: right;
  margin: 1em 0 0;
  text-align: center;
}
.action_buttons input {
  display: inline-block;
  margin: 0 0 1em 1em;
  font-size: 1.2em;
}
.action_buttons  input[name=effacer] {
  text-transform: uppercase;
}

/*********************************************************
 * Grid                                                  *
 *********************************************************/

.step-page div.grid {
  display: table;
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}
.grid-header {
  color: #fff;
  background: $blue-accent;
}
.step-page div.grid .grid-header > div {
  border-right-color: #fff;
  border-left-color: #fff;
}
.step-page div.grid > div {
  display: table-row;
}
.step-page div.grid > div:nth-child(2n) {
  background: #fff;
}
.step-page div.grid > div > div {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  border: 1px solid $gray;
}
.step-page div.grid > div:first-child div:not(:first-child) {
  font-weight: bold;
  text-align: center;
}
.step-page div.grid > div:first-child div:nth-child(2) {
  width: 110px;
}
.step-page div.grid > div:first-child div:nth-child(3) {
  margin-right: 50px;
  width: 72px;
}
.step-page div.grid > div:first-child div:nth-child(4) {
  width: 50px;
}
.step-page div.grid > div:first-child div:nth-child(5),
.step-page div.grid > div:first-child div:nth-child(6) {
  width: 60px;
}
.step-page div.grid > div:not(:first-child) div {
  font-weight: normal;
}
.step-page div.grid > div:not(:first-child) div > span.output {
  padding-bottom: 9px;
}
.step-page div.grid > div:not(:first-child) div:nth-child(5),
.step-page div.grid > div:not(:first-child) div:nth-child(6) {
  padding-bottom: 2px;
  padding-left: 8px;
}

@mixin theme-color ($themes-name, $color-accent, $color-darker, $color-dark) {
  .#{$themes-name} {
    .grid-header,
    .step-page .foot-notes,
    .breadcrumb-simulateur li p,
    .breadcrumb-simulateur li:last-child p:after {
      background: $color-accent;
    }
    .breadcrumb-simulateur li p:after {
      border-left-color: $color-accent;
    }
    .step-page input[type=text],
    .step-page input[type=date],
    .step-page input[class=date],
    .step-page input[type=number],
    .step-page input[type=money],
    .step-page .select2-container .select2-choice,
    .step-page .select2-drop,
    .step-page .emphasized,
    .step-page .explanation {
      color: $color-accent;
    }
  }
}

@include theme-color(espace-pro, $violet-accent, $violet-darker, $violet-dark);
@include theme-color(espace-asso, $green-accent, $green-darker, $green-dark);
@include theme-color(espace-annuaire, $annu-accent, $annu-darker, $annu-dark);


@media (min-width: $screen-m-min) {
  .step-page label,
  .step-page .label {
    padding-right: 10px;
    width: 50%;
    text-align: right;
  }
}
