//## site color.
$green:                 #ddeed6;
$green-dark:            #0c5e2e;
$green-light:           #ddeed6;

$orange:                #faf1d8;
//$orange-dark:           #bc5b00;
$orange-dark:           #bc5b00;
$orange-light:          #faf1d8;

