$red-light:             #f1ddde;


$brand-primary:         $blue-accent !default;
$brand-success:         $green !default;
$brand-info:            #5bc0de !default;
$brand-warning:         $orange-dark !default;
$brand-danger:          $red !default;



// Scaffolding
//
// Style guide: variables.scaffolding
//

// Background color for `<body>`.
$body-bg:               #fff !default;
// Global text color on `<body>`.
$text-color:            $gray-blue !default;

// Global textual link color.
$link-color:            $text-color !default;
// Link hover color set via `darken()` function.
$link-hover-color:      $blue-accent !default;



// Typographie
//
// Style guide: variables.typography
//

// Font, line-height, and color for body text, headings, and more.

$font-family-sans-serif:        "roboto_regular", Arial, sans-serif !default;
$font-family-sans-serif-bold:   "roboto_bold", Arial, sans-serif !default;
$font-family-sans-serif-light:  "roboto_light", Arial, sans-serif !default;

$font-family-serif:       Georgia, "Times New Roman", Times, serif !default;
// Default monospace fonts for `<code>`, `<kbd>`, and `<pre>`.
$font-family-monospace:   Menlo, Monaco, Consolas, "Courier New", monospace !default;

// Font for icon :: from icomoon app
$icon-font-family:              'fonticon';


$font-family-base:        $font-family-sans-serif !default;

$font-size-base:          14;
$font-size-base-px:       #{$font-size-base}px;
$font-size-base-em:       em($font-size-base) !default;
$font-size-large:         em(18) !default;
$font-size-small:         em(12) !default;
$font-size-large-px:      18px !default;
$font-size-small-px:      12px !default;



// heading
$font-size-h1-em:         em(36, $font-size-base) !default;
$font-size-h2-em:         em(28, $font-size-base) !default;
$font-size-h3-em:         em(24, $font-size-base) !default;
$font-size-h4-em:         em(20, $font-size-base) !default;
$font-size-h5-em:         em(18, $font-size-base) !default;
$font-size-h6-em:         em(16, $font-size-base) !default;

$headings-font-family-h1: $font-family-sans-serif-light;
$headings-font-family-h2: $font-family-sans-serif;
$headings-font-family-h3: $font-family-sans-serif;
$headings-font-family-h4: $font-family-sans-serif;
$headings-font-family-h5: $font-family-sans-serif-bold;
$headings-font-family-h6: $font-family-sans-serif-bold;

$headings-font-weight-h1: 400 !default;
$headings-font-weight-h2: 400 !default;
$headings-font-weight-h3: 400 !default;
$headings-font-weight-h4: 700 !default;
$headings-font-weight-h5: 400 !default;
$headings-font-weight-h6: 400 !default;

$color-h1: $blue-accent !default;
$color-h2: $text-color !default;
$color-h3: $blue-accent !default;
$color-h4: $gray-blue !default;
$color-h5: $blue-accent !default;
$color-h6: $text-color !default;

// Unit-less `line-height` for use in components like buttons.
$line-height-base:        1.5 !default;

// Computed "line-height" (`font-size` * `line-height`) for use with `margin`, `padding`, etc.

// grr
// /!\ pour utiliser marges calculé sur taille typo et line-height, les calculs ne passent pas tous bien
// cf http://stackoverflow.com/questions/15513395/using-dynamic-concatenated-values-for-calculations-in-sass
$line-height-computed:    ($font-size-base * $line-height-base) !default; // ~20px
$line-height-computed-px: #{($line-height-computed * $line-height-base)}px !default;
$line-height-computed-em: (1 * $line-height-base) !default; // @todo

$line-height-large:         1.33 !default;
$line-height-small:         1.5 !default;


// By default, this inherits from the `<body>`.
//$headings-font-family:    inherit !default;
//$headings-font-weight:    500 !default;
//$headings-font-weight-fontface:    normal !default;
$headings-line-height:    1.1 !default;
$headings-color:          inherit !default;

//
$font-size-lead:         em(16, $font-size-base) !default;

//** Horizontal line color.
$hr-border:                   $text-color !default;

//** Horizontal offset for forms and lists.
$component-offset-horizontal: 180px !default;



//** Text muted color
$text-muted:                  $gray-light !default;
//** Abbreviations and acronyms border color
$abbr-border-color:           $gray-light !default;
//** Headings small color
$headings-small-color:        $gray-darkest !default;
//** Blockquote small color
$blockquote-small-color:      $gray-light !default;
//** Blockquote font size
$blockquote-font-size:         em(18, $font-size-base) !default;
//** Blockquote border color
$blockquote-border-color:     $gray-lighter !default;
//** Page header border color
$page-header-border-color:    $gray-lighter !default;


//== Form states and alerts
//
//## Define colors for form feedback states and, by default, alerts.

$state-success-text:             $green-dark !default;
$state-success-bg:               $green-light !default;
$state-success-border:           $green-dark !default;

$state-info-text:                $blue-accent !default;
$state-info-bg:                  $blue-light !default;
$state-info-border:              $blue-accent !default;

$state-warning-text:             $orange-dark !default;
$state-warning-bg:               $orange-light !default;
$state-warning-border:           $orange-dark !default;

$state-danger-text:              $red-dark !default;
$state-danger-bg:                $red-light !default;
$state-danger-border:            $red-dark !default;



//== Tables
//
//## Customizes the `.table` component with basic values, each used across all table variations.

//** Padding for `<th>`s and `<td>`s.
$table-cell-padding:            8px !default;
//** Padding for cells in `.table-condensed`.
$table-condensed-cell-padding:  5px !default;

//** Default background color used for all tables.
$table-bg:                      transparent !default;
//** Background color used for `.table-striped`.
//$table-bg-accent:               #f9f9f9 !default;
$table-bg-accent:               #f2f1f0 !default;
//** Background color used for `.table-hover`.
$table-bg-hover:                #f5f5f5 !default;
$table-bg-active:               $table-bg-hover !default;

//** Border color for table and cell borders.
//$table-border-color:            #ddd !default;
$table-border-color:            $gray-lighter !default;




// Spaces
// -----------------
// margin, padding génériques


//## Define common padding and border radius sizes and more. Values based on 14px text and 1.428 line-height (~20px to start).

$padding-base-vertical     :   6px !default;
$padding-base-horizontal   :   12px !default;

$padding-base-vertical-em  :   6px !default;
$padding-base-horizontal-em:   12px !default;

$padding-large-vertical    :   8px !default;
$padding-large-horizontal  :   28px !default;

$padding-small-vertical    :   5px !default;
$padding-small-horizontal  :   10px !default;

$padding-xs-vertical       :   1px !default;
$padding-xs-horizontal     :   5px !default;

$border-radius-base        :   4px !default;
$border-radius-large       :   6px !default;
$border-radius-small       :   3px !default;

//** Global color for active items (e.g., navs or dropdowns).
$component-active-color:    #fff !default;
//** Global background color for active items (e.g., navs or dropdowns).
$component-active-bg:       $brand-primary !default;

//** Width of the `border` for generating carets that indicator dropdowns.
$caret-width-base:          4px !default;
//** Carets increase slightly in size for larger components.
$caret-width-large:         5px !default;




// Z-index
// -----------------

$zindex-navbar:            1000 !default;
$zindex-dropdown:          1000 !default;
$zindex-popover:           1010 !default;
$zindex-tooltip:           1030 !default;
$zindex-navbar-fixed:      1030 !default;
$zindex-modal-background:  1040 !default;
$zindex-modal:             1050 !default;




// Media queries breakpoints
// -------------------------

// /!\ attention /!\
// pour les déclarations des mediaqueries en em, les navigateurs utilisent la taille de police par défaut

$screen-xxs-min    : 20em !default;   // 320px
$screen-xs-min     : 25em !default;   // 360px
$screen-s-min      : 30em !default;   // 480
$screen-m-min      : 37em !default;   // 592
$screen-xm-min     : 48em !default;   // 768
$screen-l-min      : 60em !default;   // 960
$screen-xl-min     : 70em !default;   // 1184
$screen-xxl-min    : 80em !default;   // 1280

$screen-xxs-max    : ($screen-xs-min - 0.01) !default;
$screen-xs-max     : ($screen-s-min - 0.01) !default;
$screen-s-max      : ($screen-m-min - 0.01) !default;
$screen-m-max      : ($screen-xm-min - 0.01) !default;
$screen-xm-max     : ($screen-l-min - 0.01) !default;
$screen-l-max      : ($screen-xl-min - 0.01) !default;
$screen-xl-max     : ($screen-xxl-min - 0.01) !default;


// Grid system
// -----------------

$grid-gutter-width :       20px;

// Navbar collapse
// Point at which the navbar becomes uncollapsed.
$grid-float-breakpoint:     $screen-m-min !default;
// Point at which the navbar begins collapsing.
$grid-float-breakpoint-max: ($grid-float-breakpoint - 1) !default;


// Components
// -----------------
// les variables des modules sont stockés directement dans les modules

